import * as React from 'react';
// import Loader from '../../components/loader';
import Search from './search';

interface IState {
  patients: {};
  errorStatus?: string;
}

export default class SearchContainer extends React.Component<{}, IState> {
  state: IState = {
    patients: {}
  };

  async componentDidMount() {
    const response = await fetch('https://codingchalangeapi.now.sh/providers');
    console.log(response);

    if (response.status >= 400) {
      this.setState({ errorStatus: 'Error fetching groceries' });
    } else {
      response.json().then(data => {
        console.log(data);

        // this.setState({ groceries: data.groceries });
      });
    }
  }

  public render() {
    const { patients } = this.state;
    console.log(patients);

    return <Search patients={patients} />;
  }
}



